.mission {
    width: 100%;
    background-color: #A2F0B8;
    text-align: center;
    padding: 49px 16px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.title-mission {
    width: 100%;
    height: 40px;
    margin-bottom: 1em;
    font-family: 'Roboto Mono';
    font-style: normal;
    font-weight: 400;
    font-size: 30px;
    line-height: 40px;
    color: #052609;
}

.text-mission {
   
    margin-bottom: 4em;
    font-family: 'Roboto Mono';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    text-align: center;

    color: #052609;
}

.img-mission {
    width: 100px;
    height: 100px;
}