.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* Adiciona uma camada escura */
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .modal {
    background-color: #26A449;
    padding: 15px 25px;
    max-width: 500px;
    width: 100%;
    border-radius: 4px;
    position: relative;
    font-family: 'Roboto Mono';
    font-style: normal;
    padding-bottom: 0;
    z-index: 1;
  }
  
  .modal-close {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    cursor: pointer;
    background-color: brown;
    color: #fff;
    padding: 3px;
    font-weight: bold;
  }
  
  
  .modal-content {
    margin-bottom: 20px;
  }
  
  .modal-content h1 {
    margin-top: 0;
    font-size: 20px;
    margin-bottom: .5em;
  }
  
  .modal-content p {
    margin: 10px auto;

    line-height: 1.5;
    font-size: 13px;
  }

  .modal-content ul li {
    margin-left: 1em;
    line-height: 1.5;
    font-size: 13px;
    font-weight: bold;
  }
  
  .modal-content form {
    display: flex;
    flex-direction: column;
  }
  
  .modal-content input,
  .modal-content button {
    margin-bottom: 10px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 14px;
    width: 100%;
  }
  
  .modal-content button[type='submit'] {
    background-color: #0A4D1C;
    color: #fff;
    cursor: pointer;
    border: none;
  }
  
  .modal-content button[type='submit']:hover {
    background-color: #0A5D3C;
  }
  
  .number {
    cursor: pointer;
    text-decoration: underline;
  }