.whatsapp-button {
    position: fixed;
    bottom: 30px;
    right: 20px;
    width: 60px;
    height: 60px;
    background-color: transparent;
    border-radius: 50%;
    border: none;
    outline: none;
    cursor: pointer;
  }
  
  .whatsapp-button img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .messagem-whats {
    position: fixed;
    bottom: 30px;
    right: 90px;
    width: 60px;
    height: 60px;
    background-color: #fff;
    border-radius: 10px;
    width: 140px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 2px;
    text-align: center;
    font-style: normal;
    font-weight: bold;
    border: 1px solid #000;
  }
  
  .messagem-whats button {
    position: fixed;
    bottom: 75px;
    right: 94px;
    background-color: red;
    color: #fff;
    float: right;
    cursor: pointer;
    width: 10px;
    height: 10px;
    font-size: 7px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    border-radius: 6px;
  }
  